import appConfig from "@/config/config.js"
import Request from '@/plugins/luch-request/index.js'

const http = new Request();

/**
 * @description 
 * @param {Function}   
 */
http.setConfig((config) => { /* config */
	/*  */
	config.baseURL = appConfig.baseUrl;
	//  showLoading
	config.custom = {
		showLoading: true
	}
	config.header = {
		// contentType: "application/x-www-form-urlencoded"
		'Content-Type': 'application/json'
	}
	return config
})


//    ""
let requestNum = 0

/**
 * 
 */
http.interceptors.request.use((config) => {
	requestNum = requestNum + 1
	if (requestNum === 1) {
		if (config.custom.showLoading) {
			// 
			uni.showLoading({
				title: '',
				mask: true,
			});
		}
	}
	let token = uni.getStorageSync("token");
	let user_id = uni.getStorageSync("user_id");

	config.header = {
		...config.header,
		"Authorization": appConfig.authorization,
		"token": token ? token : "",
		"user_id": user_id ? user_id : "",
		"Client-Type": "WECHAT"
	}
	return config
}, config => { // async await 
	return Promise.reject(config)
})


// 
http.interceptors.response.use((res) => {
	requestNum = requestNum - 1
	if (requestNum < 1) {
		// 
		uni.hideLoading()
	}
	//
	const status = res.data.code || res.statusCode;
	const message = res.data.msg || res.data.errMsg || '';
	if (status == 200) {
		// rescodedata
		return res.data;
	}
	uni.showToast({
		icon: "none",
		title: message,
		duration: 3000
	});
	return Promise.reject("error")
}, error => {
	// console.log("**************************************   error")
	requestNum = 0
	// loading
	uni.hideLoading()
	//
	const status = error.data.code || error.statusCode || undefined;
	const message = error.data.msg || error.errMsg || '';
	uni.showToast({
		icon: "none",
		title: message,
		duration: 2000
	});
	if (status == 401) {
		uni.removeStorageSync("token");
	}
});


export default http;
